.grid-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 10px;
  width: 600px;
  height: 600px;
  margin: 0 auto;
  border: 2px solid #000;
  padding: 10px;
  border-radius: 10px;
}

.robot-icon {
  max-width: 100%;
  max-height: 100%;
  border-radius: 0;
  background-color: transparent;
}

.grid-item {
  background: linear-gradient(to bottom right, #ffd700, #ffa500, #ffffff);
  border: 2px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-item:first-child .robot {
  width: 80%;
  height: 80%;
  border-radius: 50%;
}

.robot {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.red-square {
  width: 50px;
  height: 50px;
  background-color: red;
}
