/* Default styles */
.App {
  text-align: center;
  background-color: #204969;
  font-family: Arial, sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App h1 {
  font-size: 36px;
  color: #fff;
  margin-bottom: 20px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 1px;
  background-color: #ffb400;
  margin-bottom: 20px;
}

.grid-item {
  background-color: #fff;
}

.logic-panel {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.arrow-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.arrow-buttons .direction {
  width: 50px;
  height: 50px;
  background-color: #ffb400;
  margin: 5px;
  cursor: move;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  font-size: 24px;
}

.arrow-buttons .direction.left:before {
  content: '\2190';
}

.arrow-buttons .direction.down:before {
  content: '\2193';
}

.arrow-buttons .direction.up:before {
  content: '\2191';
}

.arrow-buttons .direction.right:before {
  content: '\2192';
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.buttons button {
  margin: 5px;
}

.play-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  background-color: transparent;
  border: 2px solid #ffb400;
  color: #ffb400;
  font-size: 24px;
  cursor: pointer;
}

.play-button:hover {
  background-color: #ffb400;
  color: #fff;
}

.reset-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  background-color: transparent;
  border: 2px solid #ffb400;
  color: #ffb400;
  font-size: 24px;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #ffb400;
  color: #fff;
}

/* Media queries for responsiveness */

/* Small devices (portrait phones) */
@media only screen and (max-width: 480px) {
  .App h1 {
    font-size: 24px;
    margin-bottom: 10px;
  }

  .arrow-buttons .direction {
    width: 40px;
    height: 40px;
    margin: 3px;
    font-size: 20px;
  }

  .buttons button {
    margin: 3px;
  }
}

/* Medium devices (landscape phones, tablets) */
@media only screen and (min-width: 481px) and (max-width: 1024px) {
  .App h1 {
    font-size: 30px;
    margin-bottom: 15px;
  }

  .arrow-buttons .direction {
    width: 45px;
    height: 45px;
    margin: 4px;
    font-size: 22px;
  }

  .buttons button {
    margin: 4px;
  }
}

/* iPhone SE */
@media only screen and (device-width: 320px) and (device-height: 568px) and (-webkit-device-pixel-ratio: 2) {
  .App h1 {
    font-size: 22px;
    margin-bottom: 8px;
  }

  .arrow-buttons .direction {
    width: 38px;
    height: 38px;
    margin: 2px;
    font-size: 18px;
  }

  .buttons button {
    margin: 2px;
  }
}

/* Large devices (desktops, laptops) */
@media only screen and (min-width: 1025px) {
  .App h1 {
    font-size: 36px;
    margin-bottom: 20px;
  }

  .arrow-buttons .direction {
    width: 50px;
    height: 50px;
    margin: 5px;
    font-size: 24px;
  }

  .buttons button {
    margin: 5px;
  }
}
